.Game {
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80px;
  flex: 1;
  margin: 0;
  padding: 50px 50px 100px; }

.gameTitle {
  margin-bottom: 30px;
  text-transform: uppercase;
  color: #fff; }

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  background-color: #fff;
  padding: 50px;
  padding-bottom: 0;
  overflow: hidden;
  height: 100px; }

.isGameModePicked {
  width: 610px;
  height: 100%;
  padding: 50px; }
